	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/","/basics/quick-start-guide/","/basics/slow-start-guide/","/basics/guide-react/","/basics/guide-vue/","/basics/guide-angular/","/basics/writing-stories/","/basics/exporting-storybook/","/basics/faq/","/basics/community/"],"configurations":["/configurations/default-config/","/configurations/custom-webpack-config/","/configurations/custom-babel-config/","/configurations/add-custom-head-tags/","/configurations/serving-static-files/","/configurations/env-vars/","/configurations/cli-options/"],"testing":["/testing/react-ui-testing/","/testing/structural-testing/","/testing/interaction-testing/","/testing/css-style-testing/","/testing/manual-testing/"],"addons":["/addons/introduction/","/addons/using-addons/","/addons/addon-gallery/","/addons/writing-addons/","/addons/api/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/addons/addon-gallery/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/addons/api/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/addons/using-addons/"},"frontmatter":{"title":"Using Addons","id":"using-addons"}}},{"node":{"fields":{"slug":"/addons/writing-addons/"},"frontmatter":{"title":"Writing Addons","id":"writing-addons"}}},{"node":{"fields":{"slug":"/addons/introduction/"},"frontmatter":{"title":"Intro to Addons","id":"introduction"}}},{"node":{"fields":{"slug":"/configurations/cli-options/"},"frontmatter":{"title":"CLI Options","id":"cli-options"}}},{"node":{"fields":{"slug":"/configurations/add-custom-head-tags/"},"frontmatter":{"title":"Add Custom Head Tags","id":"add-custom-head-tags"}}},{"node":{"fields":{"slug":"/configurations/custom-babel-config/"},"frontmatter":{"title":"Custom Babel Config","id":"custom-babel-config"}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom Webpack Config","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/default-config/"},"frontmatter":{"title":"Default Config","id":"default-config"}}},{"node":{"fields":{"slug":"/configurations/env-vars/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/basics/community/"},"frontmatter":{"title":"Community","id":"community"}}},{"node":{"fields":{"slug":"/basics/guide-angular/"},"frontmatter":{"title":"Storybook for Angular","id":"guide-angular"}}},{"node":{"fields":{"slug":"/configurations/serving-static-files/"},"frontmatter":{"title":"Serving Static Files","id":"serving-static-files"}}},{"node":{"fields":{"slug":"/basics/guide-vue/"},"frontmatter":{"title":"Storybook for Vue","id":"guide-vue"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/basics/exporting-storybook/"},"frontmatter":{"title":"Exporting Storybook as a Static App","id":"exporting-storybook"}}},{"node":{"fields":{"slug":"/basics/faq/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/basics/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/basics/guide-react/"},"frontmatter":{"title":"Storybook for React","id":"guide-react"}}},{"node":{"fields":{"slug":"/basics/writing-stories/"},"frontmatter":{"title":"Writing Stories","id":"writing-stories"}}},{"node":{"fields":{"slug":"/testing/css-style-testing/"},"frontmatter":{"title":"CSS/Style Testing","id":"css-style-testing"}}},{"node":{"fields":{"slug":"/basics/slow-start-guide/"},"frontmatter":{"title":"Slow start guide","id":"slow-start-guide"}}},{"node":{"fields":{"slug":"/testing/react-ui-testing/"},"frontmatter":{"title":"Introduction: React UI Testing","id":"react-ui-testing"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/testing/structural-testing/"},"frontmatter":{"title":"Structural Testing","id":"structural-testing"}}},{"node":{"fields":{"slug":"/testing/interaction-testing/"},"frontmatter":{"title":"Interaction Testing","id":"interaction-testing"}}}]},"markdownRemark":{"html":"<p>For React, <a href=\"https://facebook.github.io/jest/blog/2016/07/27/jest-14.html\">Jest’s snapshot testing</a> is the best way to do Structural Testing.\nIt’s painless to use and maintain.\nWe’ve integrated Jest’s snapshot testing directly into Storybook using an addon called <a href=\"https://github.com/storybooks/storybook/tree/master/addons/storyshots\">StoryShots</a>.\nNow we can simply use existing stories as the input for snapshot testing.</p>\n<h2 id=\"whats-snapshot-testing\"><a href=\"#whats-snapshot-testing\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>What’s Snapshot Testing?</h2>\n<p>With Snapshot testing, we keep a file copy of the structure of UI components.\nThink of it like a set of HTML sources.</p>\n<p>Then, after we’ve completed any UI changes, we compare new snapshots with the snapshots that we kept in the file.</p>\n<p>If things are not the same, we can do two things:</p>\n<ol>\n<li>We can consider new snapshots that show the current state, and then update them as new snapshots.</li>\n<li>We can find the root cause for the change and fix our code.</li>\n</ol>\n<blockquote>\n<p>We can also commit these snapshots directly into the source code.</p>\n</blockquote>\n<h2 id=\"using-storyshots\"><a href=\"#using-storyshots\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Using StoryShots</h2>\n<p><a href=\"https://github.com/storybooks/storybook/tree/master/addons/storyshots\">StoryShots</a> is our integration between Storybook and Jest Snapshot Testing.\nIt’s pretty simple to use.</p>\n<p>First, make sure you are inside a Storybook-enabled repo (make sure it has few stories).\nThen, install StoryShots into your app with:</p>\n<pre><code class=\"language-sh\">npm i -D @storybook/addon-storyshots\n</code></pre>\n<p>Then, assuming you are using Jest for testing, you can create a test file <code>storyshots.test.js</code> that contains the following:</p>\n<pre><code class=\"language-js\">import initStoryshots from '@storybook/addon-storyshots';\n\ninitStoryshots({ /* configuration options */ });\n</code></pre>\n<p>Now you can snapshot test all of your stories with:</p>\n<pre><code class=\"language-sh\">npm test\n</code></pre>\n<p>This will save the initial set of snapshots inside your Storybook config directory.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/storyshots-first-run-d1ac58bda088901d670988e0dd8fb1e7-44d6a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 690px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 26.956521739130434%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAFCAYAAABFA8wzAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAe0lEQVQY06XQywqDMBQE0DEgogtJUjUJYmt9IRLa//+6MRCh2xIXZ3VhmLkYj4PNshBlRVSBUkSeE0Ai/SCGJzG+CGOIfSeKIh6zLCHQOeLzJbwnti1aV6Kuib7/hf/LhFZSSboQLIS4MfXSdh3f08Q5/NFaS6l14tToBEHsamxMTjnIAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"StoryShots First\"\n        title=\"\"\n        src=\"/static/storyshots-first-run-d1ac58bda088901d670988e0dd8fb1e7-44d6a.png\"\n        srcset=\"/static/storyshots-first-run-d1ac58bda088901d670988e0dd8fb1e7-f4799.png 173w,\n/static/storyshots-first-run-d1ac58bda088901d670988e0dd8fb1e7-b3b1d.png 345w,\n/static/storyshots-first-run-d1ac58bda088901d670988e0dd8fb1e7-44d6a.png 690w\"\n        sizes=\"(max-width: 690px) 100vw, 690px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>After you complete any changes, you can run the test again and find all structural changes.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/storyshots-diff-view-b97da3c22944f51da4daaf59ebe2cc08-706c7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 690px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 88.28571428571428%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"StoryShots Diff View\"\n        title=\"\"\n        src=\"/static/storyshots-diff-view-b97da3c22944f51da4daaf59ebe2cc08-44d6a.png\"\n        srcset=\"/static/storyshots-diff-view-b97da3c22944f51da4daaf59ebe2cc08-f4799.png 173w,\n/static/storyshots-diff-view-b97da3c22944f51da4daaf59ebe2cc08-b3b1d.png 345w,\n/static/storyshots-diff-view-b97da3c22944f51da4daaf59ebe2cc08-44d6a.png 690w,\n/static/storyshots-diff-view-b97da3c22944f51da4daaf59ebe2cc08-706c7.png 700w\"\n        sizes=\"(max-width: 690px) 100vw, 690px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<hr>\n<p>StoryShots also comes with a variety of customization options. Have a look at the StoryShots <a href=\"https://github.com/storybooks/storybook/tree/master/addons/storyshots\">repo</a> for more information.</p>","fields":{"slug":"/testing/structural-testing/"},"frontmatter":{"title":"Structural Testing","id":"structural-testing"}}},"pathContext":{"slug":"/testing/structural-testing/"}}

